import React from 'react'
import { Link, RouteComponentProps } from 'react-router-dom'
import { useQuery } from '@apollo/react-hooks'
import { Col, Row, Layout, Typography } from 'antd'
import { HomeHero, HomeListings, HomeListingsSkeleton } from '.'
import { displayErrorMessage } from '../../lib/utils'
import { 
  LISTINGS, Listings as ListingsData, ListingsVariables
} from '../../lib/graphql/queries'
import { ListingsFilter } from '../../lib/graphql/globalTypes'

import mapBackground from './assets/map-background.jpg'
import sanFrancisco from './assets/san-francisco.jpg'
import cancun from './assets/cancun.jpg'


const { Content } = Layout
const { Paragraph, Title } = Typography

const PAGE_LIMIT = 4
const PAGE_NUMBER = 1

export const Home = ({ history }: RouteComponentProps) => {

  const onSearch = (value: string) => {
    const trimmedValue = value.trim()
    if (trimmedValue){
      history.push(`/listings/${trimmedValue}`)
    } else {
      displayErrorMessage('Please enter a location first!')
    }
  }

  const { loading, data } = useQuery<ListingsData, ListingsVariables>(
    LISTINGS, 
    { variables: {
        filter: ListingsFilter.PRICE_HIGH_TO_LOW,
        limit: PAGE_LIMIT,
        page: PAGE_NUMBER
    }}
  )
  
  const renderListingSection = () => {
    if (loading) {
      return <HomeListingsSkeleton />
    }
    if (data) {
      return (
        <HomeListings 
        title="Premium Listings" listings={data.listings.result} />
      )
    }

    return null
  }

  return (
    <Content 
      className="home" 
      style={{ backgroundImage: `url(${mapBackground})`}}
    >
      <HomeHero onSearch={onSearch}/>

      <div className="home__cta-section">
        <Title className="home__cta-section-title">
          Your guide for all things rental
        </Title>
        <Paragraph>
          Helping you make the best decisions for your last minute locations.
        </Paragraph>
        <Link
          to="/listings/united%20states"
          className="ant-btn ant-btn-primary ant-btn-large home__cta-section-button"
        >
          Popular listings in the United States
        </Link>
      </div>

      {renderListingSection()}

      <div className="home__lisitngs">
        <Title level={4} className="home__listings-title">
          Listings of any kind
        </Title>
        <Row gutter={12}>
          <Col xs={24} sm={12}>
            <Link to="/listings/san%20francisco">
            <div className="home__listings-img-cover">
              <img src={sanFrancisco} alt="San Francisco" className="home__listings-img" />
            </div>
            </Link>
          </Col>
          <Col xs={24} sm={12}>
            <Link to="/listings/cancun">
            <div className="home__listings-img-cover">
              <img src={cancun} alt="Cancun" className="home__listings-img" />
            </div>
            </Link>
          </Col>
        </Row>
      </div>

    </Content>
  ) 
}