import React, { useState, useEffect, useRef } from 'react';
import { render } from 'react-dom';

import ApolloClient from 'apollo-boost' 
import { ApolloProvider, useMutation } from 'react-apollo'
import { StripeProvider, Elements } from 'react-stripe-elements'

import { BrowserRouter as Router, Switch, Route } from 'react-router-dom'
import { 
  AppHeader, AppHeaderSkeleton, Home, Host, 
  Listing, Listings, Login, NotFound, User, Stripe 
} from './sections'
import { Viewer } from './lib/types'
import { ErrorBanner } from './lib/components'
import { LOG_IN, LogIn as LogInData, LogInVariables }  from './lib/graphql/mutations'
import reportWebVitals from './reportWebVitals';

import { Affix, Layout, Spin } from 'antd'
import "./lib/styles.css"

const client = new ApolloClient({
    uri: '/api',  // because of "proxy": "http://localhost:9000" in package.json
    request: operation => {
        const token = sessionStorage.getItem('token')
        operation.setContext({
            headers: {
                'X-CSRF-TOKEN': token || ''
            }
        })
    }
})

const initialViewer: Viewer = {
    id: null,
    token: null,
    avatar: null,
    hasWallet: null,
    didRequest: false
}

const App = () => {
    const [viewer, setViewer] = useState<Viewer>(initialViewer)

    const [logIn, { error }] = useMutation<LogInData, LogInVariables>(LOG_IN, {
        onCompleted: data => {
            if (data && data.logIn) {
                setViewer(data.logIn)
                if (data.logIn.token) {
                    sessionStorage.setItem('token', data.logIn.token)
                } else {
                    sessionStorage.removeItem('token')
                }
            }
        }
    })
    const logInRef = useRef(logIn)
    useEffect( () => {
        logInRef.current()
    }, [])

    if (!viewer.didRequest && !error) {
        return (
            <Layout className="app-skeleton">
                <AppHeaderSkeleton />
                <div className="app-skeleton__spin-section">
                    <Spin size="large" tip="Launching Tinihouse" />
                </div>
            </Layout>
        )
    }

    const loginError = error ? (
      <ErrorBanner description="Problem with logging in: Please try again later!" /> 
    ) : null

    return (
      <Router>
        <Layout id="app">
          {loginError}
          <Affix offsetTop={0} className="app__affix-header">
            <AppHeader viewer={viewer} setViewer={setViewer} />
          </Affix>
          <Switch>
            <Route 
              exact 
              path="/" 
              component={Home} 
            />
            <Route 
              exact 
              path="/host" 
              render={props => <Host { ...props } viewer={viewer} />}
            />
            <Route 
              exact 
              path="/listing/:id" 
              render={props => (
                <Elements>
                  <Listing { ...props } viewer={viewer} />
                </Elements>
              )}
            />
            <Route 
              exact 
              path="/listings/:location?" 
              component={Listings} 
            />
            <Route 
              exact 
              path="/login" 
              render={props => <Login {...props} setViewer={setViewer} />}
            />
            <Route 
              exact 
              path="/stripe" 
              render={props => <Stripe {...props} viewer={viewer} setViewer={setViewer} />}
            />
            <Route 
              exact 
              path="/user/:id" 
              render={props => <User {...props} viewer={viewer} setViewer={setViewer} />}                     
            />
            <Route component={NotFound} />
          </Switch>
        </Layout>
      </Router>
    )
}

render(
    <ApolloProvider client={client}>
      <StripeProvider apiKey={process.env.REACT_APP_STRIPE_PUBLISHABLE_KEY as string}>
        <App />
      </StripeProvider>
    </ApolloProvider>
    ,
    document.getElementById('root')
);

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
